













import { Component, Vue } from 'vue-property-decorator'
import MainPage from '@/components/MainPage/MainPage.vue' // @ is an alias to /src
import NewUser from '@/components/NewUser/NewUser.vue' // @ is an alias to /src

@Component({
  components: {
    MainPage,
    NewUser
  },
  data () {
    return {
      showLogo: true
    }
  },
  created () {
    setTimeout(() => {
      this.$data.showLogo = false
    }, 1500
    )
  }
})
export default class Home extends Vue {}
