




















import { Component, Vue } from 'vue-property-decorator'

@Component({
  data () {
    return {
      nickname: ''
    }
  },
  methods: {
    trySaveNickname () {
      const cleanedNickname = this.$data.nickname?.replace(/\s/g, '')
      if (cleanedNickname) {
        this.$emit('childToParent', cleanedNickname)
      }
    }
  }
})

export default class Nickname extends Vue {
}
