























import { Component, Vue } from 'vue-property-decorator'

@Component({
  props: {
    user: {
      type: Object
    }
  },
  methods: {
    redirectToUser () {
      this.$router.push({ path: '/copy-user', params: { user: this.$data.user } })
    }
  }
})
export default class CopyPeopleBox extends Vue {}
